.App {
  box-sizing: border-box;
}
.datetime {
  width: 220px;
  margin-left: 20%;
}
#current {
  display: flex;
  text-align: center;
  margin-right: 15%;
  border: solid;
  border-left: none;
  padding-right: 10px;
}
.Dailyfocastlist {
  display: flex;
  /* text-align: center; */
  flex-direction: column;
  margin-top: -5px;
  row-gap: 10px;
  overflow: auto;
  max-height: 200px;
  margin: auto;
  margin-bottom: 100px;
}
.HourlyFocastinglist {
  display: flex;
  /* flex-flow: row wrap; */
  text-align: center;
  overflow: auto;
  max-width: 970px;
  margin: auto;
  column-gap: 10px;
}
.HourlyFocastinglist::-webkit-scrollbar {
  display: none;
}
.Current-weather {
  /* justify-content: center; */
  position: relative;
  text-align: center;
  color: rgb(253, 253, 253);
  margin-top: 50px;
  display: flex;
  justify-content: space-between;
}
.Weather-imagecontainer {
  text-align: center;
}
.Weather-image {
  box-sizing: border-box;
  width: 427px;
  height: 427px;
  margin-top: 50px;
}
.App-header {
  background-image: url(./photos/weather1.png);
  width: "100%";
  /* height: "100vw"; */
  background-repeat: "no-repeat";
  background-size: "contain";
  background-position: "center";
}

.App-link {
  color: #61dafb;
}
.hourfocast {
  display: flex;
  flex-direction: column;
  border: 3px groove rgb(124, 123, 123);
  border-radius: 10px;
}
.dayfocast {
  display: flex;
  width: 1200px;
  border: 3px groove rgb(124, 123, 123);
  border-radius: 10px;
  justify-content: space-evenly;
}
